import "zone.js"; // Required for Angular
import { platformBrowserDynamic } from "@angular/platform-browser-dynamic";
import AppModule from "./app/app.module";

Office.initialize = () => {
  // Bootstrap the app
  platformBrowserDynamic()
    .bootstrapModule(AppModule)
    .catch(error => console.error(error));

    var itemId = "";

    // Display error messages into Word addin
    function displayError(message) {
      // $("p.totem-addin-error").css("display", "inline");
      // $("p.totem-addin-error").text(message);
    }
    
    var result = "";
    var counter = 1;
    
    // Format XML datasource definition to JSTree JSON format
    function mapXMLToJSTreeFormat(entry, parent) {
      for (var i = 0; i < entry.childNodes.length ; i++) {
        var node = entry.childNodes[i];
        var id = counter;
        counter++;
        
        if(node.childNodes.length !== 0 && node.childNodes[0].nodeType !== 3) {
          result += '{"id": "' + id + '", "parent": "' + parent + '", "text": "' + node.nodeName + '" },';
          mapXMLToJSTreeFormat(node, id);
        } else {
          result += '{"id": "' + id + '", "parent": "' + parent + '", "text": "' + node.nodeName + '", "icon": "jstree-file" },';
        }
      }
      
      return result;
    }

    // Update XPaths Custom XML Part content used by server to generate document
    function updateXPathsCustomXmlPartContent(xmlPartId, text, path) {
      
      // getByIdAsync method provides more capabilities than getByNamespaceAsync method
      Office.context.document.customXmlParts.getByIdAsync(xmlPartId, function (result) {
        if(result.status !== "succeeded" || result.value.length === 0) {
          displayError("Error when trying to get XPaths Custom XML Part by id");
          return;
        }
        
        // Below code doesn't work on Word Online
        var xmlPart = result.value;
        xmlPart.getNodesAsync('*', function (result) {
          if(result.status !== "succeeded" || result.value.length === 0) {
            displayError("Error when trying to get XPaths node");
            return;
          }

          var node = result.value[0];
          node.getXmlAsync(function (result) {
            if(result.status !== "succeeded" || result.value.length === 0) {
              displayError("Error when trying to get node XML");
              return;
            }

            var xml = result.value;
            xml = xml.replace(
              "</xpaths>", 
              '<xpath id="' + path + aliasCounter + '"><dataBinding xpath="' + path + '" storeItemID="' + itemId + '"/></xpath></xpaths>'
            );

            node.setXmlAsync(xml, function(result) {
              if(result.status !== "succeeded" || result.value.length === 0) {
                displayError("Error when trying to set node XML");
                return;
              }
              
              createContentControl(text, path);
            });
          });
        });
      });
    }
          
    var aliasCounter = 1;
    
    // Create new Content Control and set tag with XPath path
      function createContentControl(name, tag) {
          Office.context.document.setSelectedDataAsync(
              '<pkg:package xmlns:pkg="http://schemas.microsoft.com/office/2006/xmlPackage">' 
              +   '<pkg:part pkg:name="/_rels/.rels" pkg:contentType="application/vnd.openxmlformats-package.relationships+xml" pkg:padding="512">' 
              +      '<pkg:xmlData>' 
              +         '<Relationships xmlns="http://schemas.openxmlformats.org/package/2006/relationships">' 
              +            '<Relationship Id="rId1" Type="http://schemas.openxmlformats.org/officeDocument/2006/relationships/officeDocument" Target="word/document.xml" />' 
              +         '</Relationships>' 
              +      '</pkg:xmlData>' 
              +   '</pkg:part>' 
              +   '<pkg:part pkg:name="/word/document.xml" pkg:contentType="application/vnd.openxmlformats-officedocument.wordprocessingml.document.main+xml">' 
              +      '<pkg:xmlData>' 
              +         '<w:document xmlns:w="http://schemas.openxmlformats.org/wordprocessingml/2006/main">' 
              +            '<w:body>' 
              +               '<w:p w:rsidR="00ED6193" w:rsidRDefault="006244BE">' 
              +                  '<w:sdt>' 
              +                     '<w:sdtPr>' 
              +                        '<w:alias w:val="Data value:' + tag + aliasCounter + '" />' 
              +                        '<w:tag w:val="od:xpath=' + tag + aliasCounter + '" />' 
              +                        '<w:id w:val="-1330132901" />' 
              +                        '<w:dataBinding w:prefixMappings="" w:xpath="' + tag + '" w:storeItemID="' + itemId + '" />' 
              +                        '<w:text w:multiLine="1" />' 
              +                     '</w:sdtPr>' 
              +                     '<w:sdtEndPr />' 
              +                     '<w:sdtContent>' 
              +                        '<w:r w:rsidR="00574EC3">' 
              +                           '<w:t>' + name + '</w:t>' 
              +                        '</w:r>' 
              +                     '</w:sdtContent>' 
              +                  '</w:sdt>' 
              +               '</w:p>' 
              +            '</w:body>' 
              +         '</w:document>' 
              +      '</pkg:xmlData>' 
              +   '</pkg:part>' 
              +'</pkg:package>',
              {coercionType: Office.CoercionType.Ooxml},
              function (result) {
                  aliasCounter++;
              }
          );
    }
    
    // Create XPaths Custom XML Part if it does not exist, then manage its content
    function manageXPathsCustomXMLPart(text, path) {
      Office.context.document.customXmlParts.getByNamespaceAsync("http://opendope.org/xpaths", function (result) {
        if(result.status !== "succeeded") {
          console.log("Error when trying to get XPaths Custom XML Part by namespace");
          return;
        }

        var xmlPart = result.value;
        if(xmlPart.length === 0) {
          Office.context.document.customXmlParts.addAsync(
            '<?xml version="1.0" encoding="UTF-8"?>\n\
            <xpaths xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema" xmlns="http://opendope.org/xpaths"></xpaths>',
            function (result) {
              if(result.status !== "succeeded") {
                displayError("Error when creating XPaths Custom XML Part");
                return;
              }

              updateXPathsCustomXmlPartContent(result.value.id, text, path);
            }
          );
        } else {
          updateXPathsCustomXmlPartContent(xmlPart[0].id, text, path);
        }
      });
    }

    $(function() {
      // Build a JSTree based on Custom XML Part datasource definition contained in template document
      Office.context.document.customXmlParts.getByNamespaceAsync("http://opendope.org/datasourceDefinition", function (result) {
        if(result.status !== "succeeded" || result.value.length === 0) {
          displayError("Error when trying to get datasource definition Custom XML Part by namespace from current template document");
          return;
        }

        var xmlPart = result.value;
        if (xmlPart.length !== 0) {
          itemId = xmlPart[0].id;
          
          Office.context.document.customXmlParts.getByIdAsync(itemId, function (result) {
            if(result.status !== "succeeded") {
              console.log("Error when trying to get datasourcedefinition Custom XML Part by id");
              return;
            }
            
            var xmlPart = result.value;
            xmlPart.getXmlAsync(function (result) {
              if(result.status !== "succeeded") {
                return;
              }
              
              var xml = $.parseXML(result.value);
              var json = mapXMLToJSTreeFormat(xml, "#");
              json = json.substring(0, json.length - 1);
              
              var body = '{"core": {"data": [TREE]}}';
              var data = body.replace("TREE", json);
              
              $('#tree').jstree($.parseJSON(data));
            });
          });
        }
      });

      // Manage JSTree actions 
      $("#tree").on("select_node.jstree", function (e, data) { 
        var text = data.node.text;
        var path = "/" + $('#tree').jstree("get_path", data.node.id, "[1]/") + "[1]";

        var menu = [{
          name: 'Valeur de donnÃ©es',
          title: 'Valeur de donnÃ©es',
          subMenu: [{
            name: 'InsÃ©rer un contrÃ´le de contenu',
            title: 'InsÃ©rer un contrÃ´le de contenu',
            fun: function () {
              manageXPathsCustomXMLPart(text, path);
            }
          }]
        }];
        
        $(data.event.target).contextMenu(menu);
      });
    });
};
